@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
body {
  margin: 0 0 55px 0;
}

.nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 55px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
  background-color: black;
  display: flex;
  overflow-x: auto;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  min-width: 50px;
  overflow: hidden;
  white-space: nowrap;
  font-family: sans-serif;
  font-size: 13px;
  color: white;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  transition: background-color 0.1s ease-in-out;
}

.nav__link:hover {
  background-color: #eeeeee;
}

.nav__link--active {
  color: #860606;
}

.nav__icon {
  font-size: 18px;
}

body {
  background: rgb(226, 226, 226);
  font-family: "Open Sans", sans-serif;
}

.search {
  width: 100%;
  position: fixed;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #860606;
  border-right: none;
  padding: 5px;
  height: 2.1em;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #860606;
}

/* .searchTerm:focus {
  color: #860606;
} */

.searchButton {
  width: 60px;
  height: 1.7em;
  border: 1px solid #860606;
  background: #860606;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.wrap {
  z-index: 2;
  width: 80%;
  position: fixed;
  top: 2%;
  left: 50%;
  transform: translate(-50%, -50%);
}

svg.MuiSvgIcon-root {
  width: 1em;
  height: 1em;
  font-size: 2rem;
}

.header-search {
  text-align: center;
  margin-top: 6vh;
}

.loading {
  position: fixed;
  top: 25vh;
}

h1.title {
  display: flex;
  flex-direction: column;
  text-align: center;
  background: url(/static/media/manga-header.576053ef.jpg) center;
  background-repeat: no-repeat;
  position: relative;
  color: white;
  text-shadow: -5px -1px 0 #000, 1px -1px 0 #000, -5px 1px 0 #000,
    -5px 1px 0 #000;
  padding-top: 38%;
  padding-bottom: 15%;
}

.small {
  display: inline-block;
  flex-direction: column;
  width: 40%;
  background: rgb(203 195 195);
  margin: 5%;
  margin-left: 5vw;
  padding: 0vw;
  text-align: center;
  border-radius: 10%;
}

.home-image {
  max-width: 100%;
  display: flex;
  margin: inherit;
  margin-top: 1.5vh;
}

.modal {
  position: fixed;
  top: 5%;
  right: 8%;
  bottom: 10%;
  left: 8%;
  padding: 7px;
  box-shadow: 20px 20px 20px 20px rgba(127, 127, 127, 0.3);
  background: rgb(255 255 255 / 95%);
}

.scrollbox {
  height: 85vh;
  overflow-y: scroll;
}

/* $primary: #5663ff;
$lightGray: #eff1f6; */

.form-section {
  background: rgb(226, 226, 226);
  max-width: 550px;
  margin: 3vh auto 0;
  padding: 0 20px;
  max-height: 75vh;
  top: 50%;
  left: 50%;
}

.form-section .round-image {
  height: 250px;
  width: 250px;
  border-radius: 100%;
  overflow: hidden;
  margin: 0 auto 20px;
}

.form-section .round-image img {
  object-fit: cover;
  height: 100%;
  object-position: center;
}

.form-section .header {
  text-align: center;
}

.form-section .headersignin {
  margin-top: 17vh;
  text-align: center;
}

.form {
  background: black;
  padding: 30px;
  border-radius: 20px;
  box-shadow: rgba(16, 35, 82, 0.02) 0 7px 50px 20px;
}

.form .title {
  margin: 10px 0;
}

.form-group .input {
  margin: 8px 0 12px;
  width: 100%;
  border-radius: 4px;
  display: block;
  border: none;
  font-size: 14px;
  background: #eff1f6;
  padding: 12px 12px;
}

.form-group .input-error {
  padding: 8px 16px;
  border-radius: 4px;
  background-color: rgb(255, 192, 192);
  color: rgb(255, 34, 34);
}

.form-group .text-area {
  display: block;
  font-size: 14px;
  width: 100%;
  background: #eff1f6;
  border: none;
  border-radius: 4px;
  line-height: 1.3;
  resize: none;
  box-sizing: border-box;
  padding: 12px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Manrope", sans-serif;
}

.form-group .label {
  font-weight: 600;
  color: white;
}

.form button {
  margin-top: 30px;
  background: #860606;
  width: 100%;
  color: white;
  padding: 16px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
}

.form-section.link {
  text-align: center;
}

.form-section.link a {
  color: blue;
  text-decoration: underline;
}

h2 {
  color: white;
  text-align: center;
}

.profile-title {
  text-align: center;
}

.profile-input {
  width: 12vw;
}

.profile-img {
  display: flex;
  margin: auto;
  /* margin-left: 2px; */
  align-items: center;
}

.smaller {
  display: inline-block;
  /* position: absolute; */
  flex-direction: column;
  width: 40%;
  background: rgb(203 195 195);
  margin: 5%;
  margin-left: 5vw;
  padding: 0vw;
  text-align: center;
  border-radius: 10%;
}

